// General reset of margins

*,
*::after,
::before {
  margin-top: 0;
}

// Owl reset of top margins

* + * {
  margin-top: 0em;
  margin-bottom: 0em;
}
// second paragraph or second li margin top 

p + p,
li + li
 {
  margin-top: 1rem;
}

// General headings

h1,
h2,
h3,
dt,
small,
.small,
dl#tweetlist dt,
footer,
label,
cite,
div.blogparent,
input[type="submit"] {
  font-family: var(--font-baskerville);
  font-weight: normal;
  font-style: normal;
}
// Masthead

h1 {
  margin-top: 0.5rem;
  font-size: 2em;
  line-height: 1em;
  margin-bottom: 1.5em;
}

// h3 {
//   font-size: 1.4rem;
// }

a {
  text-decoration: none;
  color: var(--clr-link);
  overflow-wrap: break-word;
  word-wrap: break-word;
}
a:visited {
  color: var(--clr-link_visited);
}
a:hover {
  text-decoration: underline;
}

// Show external links 

a:not(.icon)[href^="http"]::after {
  content: url(/images/external.svg);
  // padding-right: 13px;
}

@media (prefers-color-scheme: dark) {
  a:not(.icon)[href^="http"]::after {
    content: url(/images/external_dark.svg);
    // padding-right: 13px;
  }

}

h1,
h1 > a,
h1 a:visited,
h1 > a:active,
h1 > a:hover {
  color: var(--clr-headings);
}

h2 {
  color: var(--clr-headings);
}

h2 a {
  color: var(--clr-headings);
  text-decoration: underline;
}

p {
  line-height: 1.8rem;
  text-align: left;
}

p, dl {
  display: block;
  margin-block-start: 1rem;
  margin-block-end: 1rem;
}
